import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import {$http, Qs, DateF} from '../../tools';
import DatePicker from 'react-mobile-datepicker';
import {
  BrowserRouter as Router,
  NavLink as Link,
  Redirect
} from 'react-router-dom';

/*import initReactFastclick from 'react-fastclick';
initReactFastclick();*/

const params = Qs();


let Interval = null;

@inject('custodybank')
@observer
export default class custodyBank extends Component {
  constructor(props) {
    super(props);
        this.timer = null;
  }
  
  componentDidMount() {
    this.props.custodybank.getUserInfo();
  }
  componentWillUnmount() {
    clearInterval(Interval);
  }
  render() {
    document.title = '';
        const {
            bankNo, getBankNo, bankName, mobile,userName,userId,
            period, 
            smsCode, sendSmsCode, codeText,
            canApply, verifyRequired,
            show, tipStr, showTips,
            confirmBindCard, handleChange,getUserInfo,isBinded,
            open, time, openDatePicker, selectDate,goSupport
        } = this.props.custodybank;

    return (
      <div className="bindCredit">
            <div className="mailbox-wrap">
              <ul className="input-list">
                  <li>
                    <div className="label"></div>
                    <div className="input-c">
                      {userName}
                    </div>
                  </li>
                  <li>
                    <div className="label"></div>
                    <div className="input-c">
                      {userId}
                    </div>
                  </li>
                  <li>
                    <div className="label"></div>
                    <div className="input-c">
                      <span style={{display:isBinded ? 'none': 'block'}}>{bankNo}</span>
                      <input style={{display:isBinded ? 'block': 'none'}} type="number" value={bankNo} placeholder="" onChange={getBankNo}/>
                    </div>
                  </li>
                  <li className="help-info">
                    <div className="label"></div>
                    <img src={require('../../images/apply_help_icon.png')} alt=""  onClick={goSupport} />
                    <div className="input-c">
                    {bankName}
                    </div>
                  </li>
                </ul>
                <ul className="input-list">
                  <li className="help-info">
                    <div className="label"></div>
                    <div className="input-c">
                      <span style={{display:isBinded ? 'none': 'block'}}>{mobile}</span>
                      <input style={{display:isBinded ? 'block': 'none'}} type="tel" value={mobile} placeholder="" onChange={(event)=>handleChange('mobile', event.target.value)} />
                    </div>
                  </li>
                    <li style={{display:isBinded ? 'block': 'none'}}>
                    <div className="label"></div>
                    <div className="input-c sec-code">
                      <input type="tel" value={smsCode} onChange={(event)=>handleChange('smsCode', event.target.value)} placeholder="" />
                      <i className="sec-time" onClick={sendSmsCode}>{codeText}</i>
                    </div>
                  </li>
                </ul>
               {/* <!-- submit-open -->*/}
               <div className="custody-tips">
                <span className="red"></span><span className="red"></span>
               </div>
                <a href="javascript:;" style={{display:isBinded ? 'block': 'none'}}  className={canApply ? "submit-btn submit-open" : "submit-btn"} onClick={()=>{ canApply ? confirmBindCard():''}}></a>
                <a href={`#/custodydetail`} className="custody-introduction"></a>
                <span className="safe" style={{'marginTop':'4rem'}}><img src={require('../../images/dp.png')} /></span>
            </div>
      </div>
      
    )
  }
}




